.j-stake-container {
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
  .j-liquidity-stake {
    width: 100%;
  }
  .j-input .ant-input-group-addon {
    padding: 0 20px;
  }
}
.fw600 {
  font-weight: 600;
}

.rent-unit {
  font-size: 16px;
}

.energy-max {
  height: 36px;
  line-height: 36px;
  font-weight: 400;
  font-size: 14px;
  color: #9195fb;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  padding-right: 20px;
  margin-right: 20px;
  cursor: pointer;
}

.equal-alike {
  position: relative;
  top: -6px;
  display: inline-block;
  width: 14px;
  margin-right: 3px;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;
}

.j-time-tips {
  padding: 0 20px;
}
.j-time-tips,
.j-time-tips > span {
  font-size: 12px;
  line-height: 17px;
  text-align: right;
  color: rgba(255, 255, 255, 0.6);
  margin-top: 10px;
}

.energy-infos {
  padding: 16px 20px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 5px;
}

.energy-infos-new {
  padding: 0;
  .energy-info {
    margin-bottom: 0 !important;
    &:first-child {
      padding: 12px 20px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.06);
    }
    &:nth-child(2),
    &:nth-child(3) {
      margin-top: 10px;
      padding: 0 20px;
    }
    &:nth-child(3) {
      padding-bottom: 10px;
    }
  }
  .j-tooltip-icon {
    transform: translateY(2px) !important;
  }
}

.j-energy-rent {
  .ant-input-group.ant-input-group-compact > * {
    &:first-child {
      margin-right: 0;
    }
    &:nth-child(2) {
      margin-right: -1px;
    }
  }
  .j-tooltip-icon {
    transform: translateY(-1px);
  }
  .j-exchange-icon {
    margin-left: 6px;
    margin-top: -3px;
  }
  .top-header {
    .desc {
      .learn-more {
        em {
          display: inline-block;
          width: 7px;
          height: 20px;
          margin-left: 4px;
          background: url(../../images/v2/liquidity-stake/unlock-arrow.svg) no-repeat center;
          vertical-align: top;
        }
      }
    }
  }
}

.j-eles {
  display: flex;
  > div {
    width: 50%;
    height: 556px;
    color: #fff;
    border-radius: 10px;
    position: relative;

    &:first-child {
      margin-right: 20px;
      background: url(../../images/v2/energy-rent/energy-bg.png) no-repeat center #31333a;
      background-size: cover;
    }
    &:last-child {
      background: url(../../images/v2/energy-rent/energy-form-bg.png) no-repeat center #31333a;
      background-size: cover;
    }
  }
}

.my-rent-info {
  padding: 40px 30px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  .rent-info-icon {
    width: 184px;
    height: 200px;
    background: url(../../images/v2/energy-rent/energy-icon.png) 0 0 no-repeat;
    background-size: cover;
    transform: translate(-20px, -25px);
  }
  .rent-info-top {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }

  .rent-info-content {
    margin-top: 40px;
    .rent-info-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;

      color: #ffffff;
    }
  }

  .wallet-connect {
    height: 40px;
    line-height: 40px;
    border: 1px solid rgba(145, 149, 251, 0.8);
    border-radius: 2px;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    color: #9195fb;
    margin-top: 50px;
    cursor: pointer;
    &.hide {
      visibility: hidden;
    }
  }
  &.login .energy-info {
    background-image: none;
  }
  .energy-info {
    padding: 24px 20px;
    background: url(../../images/v2/energy-rent/energy-info-bg.png) 0 0 no-repeat rgba(255, 255, 255, 0.06);
    background-size: cover;
    margin-top: 20px;
    border-radius: 10px;

    &.not-login-info {
      margin-top: 60px;
    }

    .energy-info-top {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;

      color: #ffffff;
    }
    .energy-info-content {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
      &.login {
        margin-top: 0;

        .title {
          margin-top: 0;
        }
      }
      > div {
        width: 40%;
        text-align: left;
        &:nth-child(1) {
          width: 35%;
        }
        &:nth-child(3) {
          width: 25%;
        }
        > div {
          &:first-child {
            font-weight: 500;
            font-size: 12px;
            line-height: 17px;
            color: #ffffff;
          }

          &.title,
          &.title > .title-top {
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 17px;
            color: #ffffff;
            margin-top: 10px;
            > span {
              font-weight: 400;
              font-size: 14px;
              margin-left: 4px;
            }
          }
          &.title > .title-top {
            line-height: 18px;
            margin-top: 0;
            margin-left: 0;
          }
          &.value {
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            color: #ffffff;
            margin-top: 10px;
            > span {
              font-weight: 400;
              font-size: 14px;
              line-height: 19px;
              color: #ffffff;
              margin-left: 4px;
            }
          }
        }
      }
      .vertical-line {
        margin: 0 6px;
      }
    }
    .rent-left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 24px;
      padding-top: 24px;
      border-top: 1px solid rgba(255, 255, 255, 0.06);
    }
    .rent-left-label {
      display: inline-flex;
      align-items: center;
      font-size: 12px;
      cursor: pointer;
    }
    .rent-left-time {
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      color: #fff;
      margin-top: 6px;
    }
    .rent-left-time .rent-left-wavyline {
      font-size: 12px;
      font-weight: normal;
      vertical-align: middle;
    }
    .rent-back {
      min-width: 70px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border: 1px solid rgba(145, 149, 251, 0.8);
      border-radius: 2px;
      font-weight: 500;
      font-size: 12px;
      color: #9195fb;
      cursor: pointer;
      // &:hover {
      //   background: #4c54ff;
      //   color: #fff !important;
      // }
    }
  }
  .need-stake,
  .need-burn {
    > div {
      margin-top: 10px;
      &:first-child {
        width: 24px;
        height: 24px;
        background: url(../../images/v2/energy-rent/need-stake-icon.svg) 0 0 no-repeat;
        background-size: 100%;
        margin-top: 0;
      }
      &:nth-child(2) {
        font-weight: 500;
        font-size: 12px;
        line-height: 17px;
        color: #ffffff;
      }
      &:nth-child(3) {
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        color: #ffffff;
        > span {
          font-family: 'Avenir Next';
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
          margin-left: 6px;
        }
      }
    }
  }
  .need-burn {
    > div:first-child {
      background-image: url(../../images/v2/energy-rent/need-burn-icon.svg);
    }
  }
  .rent-energy-amount {
    margin-top: 20px;
    .rent-amount {
      font-weight: 700;
      font-size: 30px;
      line-height: 41px;
      color: #ffffff;
    }
    .rent-unit {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #ffffff;
      margin-left: 4px;
    }
    &:hover {
      border-bottom-color: #9195fb;
      .rent-amount,
      .rent-unit {
        color: #9195fb;
      }
    }
  }
  .rent-delegate-amount {
    font-size: 14px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.6);
    margin-top: 20px;
  }
}

.rent-form {
  position: relative;
  background: #31333a;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);

  .j-input {
    .ant-input {
      font-weight: 600;
      font-size: 20px;
      color: #ffffff;
    }
    .ant-input-group-addon::after {
      display: none;
    }

    ::-webkit-input-placeholder {
      font-weight: 500;
      font-size: 16px;
      color: #ffffff;
      opacity: 0.4;
    }
  }
  .rent-form-tabs {
    padding: 26px 30px 17px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
    > span {
      display: inline-block;
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      color: #ffffff;
      position: relative;
      cursor: pointer;
      opacity: 0.4;
      &.current {
        opacity: 1;
        &::after {
          content: '';
          display: block;
          width: 100%;
          height: 2px;
          background: #4c54ff;
          border-radius: 1px;
          transform: matrix(1, 0, 0, -1, 0, 0);
          position: absolute;
          bottom: -18px;
        }
      }
      &:first-child {
        margin-right: 40px;
      }
      &.single-tab {
        cursor: default;
        opacity: 1;
      }
    }
  }
  .rent-content {
    padding: 0 30px 40px;
    .ant-input-group {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: rgba(41, 43, 50, 0.7);
      border-radius: 5px;
      .ant-select {
        min-width: 130px;
      }
      .energy-input {
        width: calc(100% - 130px);
        margin-top: 0;
        .ant-input-affix-wrapper {
          padding: 0 0 0 16px;
          background-color: transparent;
        }
        .ant-input {
          padding-right: 16px;
          background-color: transparent;
          // padding-left: 20px;
        }
        .ant-input-wrapper {
          background-color: transparent;
        }
      }
      .ant-input-group-addon {
        width: auto;
        display: flex;
        align-items: center;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        background-color: transparent;
        .exchange {
          display: none;
        }
      }
      .j-error-tip {
        display: flex !important;
        background: #313239;
        box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        .j-safe-text,
        .j-safe-text > span {
          font-weight: 500;
          font-size: 12px;
          line-height: 20px;
          color: #ff5266;
        }
      }
    }
  }
  .rent-label {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
    margin-top: 20px;
  }
  .rent-labels {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding: 0 20px;
    .rent-label {
      margin-top: 0;
    }
    .rent-days-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      color: rgba(255, 255, 255, 0.6);
    }
    .rent-days-value {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      color: #ffffff;
      margin: 0 0 0 4px;
    }
    .rent-max {
      font-weight: 500;
      font-size: 12px;
      line-height: 17px;
      color: #9195fb;
      cursor: pointer;
    }
  }
  .energy-input {
    margin-top: 10px;
    margin-bottom: 0;
    .anticon {
      > svg {
        display: none;
      }
    }
  }

  .energy-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    .info-subtitle {
      color: rgba(255, 255, 255, 0.6);
      border-bottom-color: rgba(255, 255, 255, 0.6);
      margin-right: 20px;
      max-width: 290px;
      line-height: 16px;
      &.pre-title {
        color: #fff;
      }
      br {
        display: none;
      }
      &.en-US {
        br {
          display: block;
        }
      }
    }

    .rent-info-ellipsis {
      width: calc((100% - 90px));
      > div {
        width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
  }
  .energy-info-pay {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 16px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.06);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    .pay-title {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #ffffff;
    }
    .pay-amount {
      display: flex;
      align-items: center;
      max-width: calc(100% - 70px);
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      color: #e9b361;
      margin-left: 6px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      > span {
        font-weight: 500;
        font-size: 14px;
        margin-left: 4px;
      }
      &.pay-error {
        color: #ff5266;
      }
    }
  }
  .rent-now {
    position: absolute;
    width: calc(100% - 60px);
    left: 30px;
    bottom: 40px;
    border-radius: 5px;
  }
  .add-after {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px 15px;
    background: rgba(255, 255, 255, 0.02);
    border-radius: 5px;
    margin-top: 10px;
    > div {
      text-align: center;
      &:nth-child(1),
      &:nth-child(3) {
        width: calc(50% - 23px);
        > div {
          display: flex;
          justify-content: center;
          align-items: center;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
      }
      &:nth-child(1) {
        padding: 0 15px 0 0px;
      }
      &:nth-child(3) {
        padding: 0 0px 0 15px;
      }
    }
  }
  .after-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;

    .after-title-with-go-up {
      position: relative;
      .go-up-icon {
        position: absolute;
        left: 100%;
      }
    }
  }
  .after-subtitle {
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: #ffffff;
    opacity: 0.6;
    margin-top: 4px;
    &.date > span {
      display: flex;
      flex-wrap: wrap;
    }
    .keep-line {
      white-space: pre-line;
      display: inline-block;
      margin-left: 2px;
    }
  }
}

.go-up-icon {
  display: inline-block;
  width: 23px;
  height: 21px;
  background: url(../../images/v2/energy-rent/go-up-icon.svg) 0 2px no-repeat;
  background-size: 100%;
}

.add-to-icon {
  display: inline-block;
  width: 46px;
  height: 8px;
  background: url(../../images/v2/energy-rent/add-to-icon.svg) 0 0 no-repeat;
  background-size: 100%;
}

.rent-select {
  height: 60px;
  &::after {
    content: '';
    display: block;
    height: 36px;
    width: 1px;
    background: rgba(255, 255, 255, 0.1);
    position: absolute;
    right: 0;
    top: 12px;
    z-index: 1;
  }
  .ant-select-selector {
    height: 60px !important;
    border: none !important;
    background: transparent !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
    border-top-left-radius: 5px !important;
    border-bottom-left-radius: 5px !important;
  }
  .ant-select-arrow {
    right: 20px;
  }
  .ant-select-selection-item {
    display: flex;
    align-items: center;
    padding-right: 34px !important;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
  }
  .anticon {
    display: inline-block;
    width: 8px;
    height: 4px;
    background: url(../../images/v2/energy-rent/up-arrow-white.svg) 0 0 no-repeat;
    background-size: 100%;
    transform: rotate(180deg);
    vertical-align: middle;
    > svg {
      display: none;
    }
  }
  &.ant-select-focused {
    .ant-select-selector {
      box-shadow: none !important;
    }
  }
  &.ant-select-open {
    .ant-select-selection-item {
      color: #fff !important;
    }
    .anticon {
      transform: rotate(0deg);
    }
  }
}

.rent-select-dropdown {
  background: #2e303d !important;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  padding: 10px 0;

  .ant-select-item-option-content {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    color: #ffffff;
  }
  .ant-select-item {
    padding: 10px 20px !important;
    &.ant-select-item-option-active {
      background: #2e303d;
      .ant-select-item-option-content {
        color: #9195fb;
      }
    }
    &.ant-select-item-option-selected {
      background: rgba(145, 149, 251, 0.1);
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      .ant-select-item-option-content {
        color: #9195fb;
      }
    }
  }
}

.stake-trx {
  background: url(../../images/v2/liquidity-stake/input-TRX.svg) no-repeat left center;
  background-size: 22px;

  height: 26px;
  margin-right: 4px;
  display: block;
  text-indent: 32px;
  line-height: 26px;
  font-weight: normal;
  font-size: 16px;
}

.ways-warning {
  > div {
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: #ffffff;
    .set-max,
    .set-max > span {
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      color: #9195fb;
      margin-left: 4px;
      cursor: pointer;
    }
  }
}

.j-wrapper.white {
  .energy-max {
    border-right: 1px solid rgba(34, 35, 43, 0.1);
  }
  .equal-alike {
    color: #22232b;
  }
  .energy-infos {
    background-color: rgba(34, 35, 43, 0.02);
  }
  .ways-warning {
    background-color: #fff !important;
    > div {
      color: #22232b;
      .set-max,
      .set-max > span {
        color: #4c54ff;
      }
    }
  }
  .rent-select .anticon {
    background-image: url(../../images/v2/white-theme/energy-rent/up-arrow-white.svg);
  }
  .my-rent-info {
    background-color: #ffffff;

    .rent-info-icon {
      background-image: url(../../images/v2/white-theme/energy-rent/energy-icon.png);
    }
    .rent-info-content {
      .rent-info-title {
        color: #22232b;
      }
    }
  }
  .j-eles > div {
    color: #22232b;
    &:first-child {
      background: url(../../images/v2/white-theme/energy-rent/energy-bg.svg) no-repeat center top/cover #ffffff;
    }
    &:last-child {
      background: url(../../images/v2/white-theme/energy-rent/energy-form-bg.svg) no-repeat center top/cover #ffffff;
    }
  }
  .my-rent-info .rent-energy-amount {
    .rent-unit,
    .rent-amount {
      color: #22232b;
    }
    &:hover {
      border-bottom-color: #4c54ff;
      .rent-amount,
      .rent-unit {
        color: #4c54ff;
      }
    }
  }
  .my-rent-info.login .energy-info {
    background-image: none;
  }
  .my-rent-info .energy-info {
    background: url(../../images/v2/white-theme/energy-rent/energy-info-bg-white.png) no-repeat center center/ 100% 100%
      rgba(34, 35, 43, 0.03);
    .energy-info-top {
      color: #22232b;
    }
    .energy-info-content > div {
      > div:nth-child(2),
      > div:nth-child(3) {
        color: #22232b;
        span {
          color: #22232b;
        }
      }
    }
    .rent-left {
      border-top: 1px solid rgba(34, 35, 43, 0.06);
    }
    .rent-left-time,
    .rent-left-time > span {
      color: #22232b;
    }
  }
  .my-rent-info .rent-delegate-amount {
    color: rgba(34, 35, 43, 0.6);
  }
  .my-rent-info .energy-info {
    .rent-back {
      color: #4c54ff;
      border: 1px solid rgba(76, 84, 255, 0.8);
    }
    .energy-info-content {
      .title,
      .title > .title-top {
        color: #22232b;
      }
    }
  }
  .rent-form {
    background: #ffffff
      linear-gradient(
        325.89deg,
        rgba(76, 212, 255, 0) 76.64%,
        rgba(16, 139, 114, 0.25) 110.29%,
        rgba(63, 255, 216, 0.83) 119.81%
      );
    .rent-form-tabs {
      border-bottom: 1px solid rgba(34, 35, 43, 0.06);
    }

    .rent-label,
    .rent-form-tabs > span {
      color: #22232b;
    }
    .energy-info {
      .info-subtitle {
        color: rgba(34, 35, 43, 0.6);
        border-bottom-color: rgba(34, 35, 43, 0.6);
        &.pre-title {
          color: #22232b;
        }
      }
    }
    .j-input {
