@media (min-width: 798px) {
  .rent-form .j-input .ant-input {
    font-size: 14px;
  }
  .rent-form .rent-labels-time,
  .rent-form .rent-labels .rent-label-text {
    display: none;
  }
  .pay-amount {
    font-size: 16px;
  }
  .pay-amount-sub {
    font-size: 12px;
  }
}
