  .j-time-tips > span {
    text-align: left;
  }
  .my-rent-info .need-stake > div:first-child {
    background-image: url(../../images/v2/energy-rent/need-stake-icon-m.png);
  }
  .my-rent-info .need-burn > div:first-child {
    background-image: url(../../images/v2/energy-rent/need-burn-icon-m.png);
  }
  .rent-form .after-subtitle.date {
