  .j-stake-container {
    width: unset;
    margin: 0 15px;
    .energy-unit {
      line-height: 20px;
      font-size: 14px;
      font-weight: 400;
    }
    .stake-trx {
      height: 20px;
      width: 50px;
      text-indent: 25px;
      background-size: contain;
      line-height: 20px;
      font-size: 14px;
    }
    .j-liquidity-stake {
      width: unset;
    }
    .top-header {
      margin: 20px 0;
    }
    .j-eles {
      flex-direction: column-reverse;
      & > div {
        width: unset;
        height: unset;
      }

      .rent-form {
        margin-bottom: 20px;
        .rent-label {
          font-size: 12px;
          line-height: 17px;
        }
        .rent-form-tabs {
          padding: 26px 0 20px 15px;
          > span {
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            &:first-child {
              margin-right: 17px;
            }
          }
          .current {
            &::after {
              bottom: -21px;
              width: 40%;
              left: 0;
              right: 0;
              margin: auto;
            }
          }
        }
        .rent-content {
          padding: 0 15px 40px;
          .go-up-icon {
            width: 16px;
            height: 16px;
            background-size: contain;
            flex: 0 0 auto;
          }
          .energy-infos {
            margin-top: 20px;
          }
          .add-after {
            padding: 18px 12px 18px 12px;
            .after-title {
              display: flex;
            }
            .after-subtitle {
              display: flex;
              .keep-line {
                margin-left: 2px;
              }
            }
            & > div {
              &:nth-child(1),
              &:nth-child(3) {
                padding: 0;
                width: calc(50% - 10px);
                // width: unset;
              }
              // &:nth-child(3) {
              //   padding-left: 30px;
              // }
            }
          }
          .after-rent {
