              flex-direction: column;
              align-items: flex-start !important;
              max-width: 50%;
              .j-tooltip-icon {
                display: none;
              }
              span {
                font-size: 12px;
              }
              .learn-more {
                font-size: 12px;
              }
            }
            .rent-info-value,
            span {
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
            }
          }
          .add-to-icon {
            flex: 0 0 20px;
            width: 20px;
            background: url('../../images/v2/energy-rent/add-to-icon-mobile.svg') no-repeat center/cover;
          }
          .ant-input-group {
            .j-select-alike.j-select-alike-energy {
              display: none !important;
            }
            .energy-input {
              width: 100%;
              .ant-input-affix-wrapper {
                padding: 0 12px;
                background-color: transparent;
              }
              .ant-input {
                padding-left: 0;
                &::placeholder {
                  font-size: 14px;
                  font-weight: 500;
                }
              }
            }
            .ant-input-group-addon {
              display: flex;
              align-items: center;
              padding: 0 12px;

              .exchange {
                margin-left: 10px;
