          .rent-labels {
            margin-bottom: 12px;
            align-items: flex-start;
            margin-bottom: 2px;
            justify-content: space-between !important;
            padding: 0 12px;
            .rent-label {
              font-weight: 400;
              font-size: 12px;
              line-height: 17px;
            }
          }
          .rent-now {
