              }
              .value {
                margin-top: 10px;
                font-weight: 600;
                font-size: 18px;
                line-height: 25px;
                span {
                  font-weight: 400;
                  font-size: 14px;
                  line-height: 19px;
                  margin-left: 4px;
                }
              }
            }
          }
        }
        .rent-info-top {
          position: relative;
          .rent-info-content {
            margin-top: 40px;
            z-index: 8;
            .rent-info-title {
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
            }
            .rent-energy-amount {
              margin-top: 12px;
              .rent-amount {
                font-size: 24px;
                line-height: 33px;
              }
              .rent-unit {
