            .rent-delegate-amount {
              margin-top: 12px;
              span {
                font-weight: 500;
                font-size: 12px;
                line-height: 20px;
              }
              .j-tooltip-icon {
                display: none;
              }
            }
          }
          .rent-info-icon {
            position: absolute;
            width: 92px;
            height: 100px;
            right: 5px;
            top: 27px;
            transform: translate(0px, 0px);

            background-image: url(../../images/v2/energy-rent/energy-icon-m.png) no-repeat center/100%;
          }
        }
        .energy-info {
          margin-top: 72px;
          padding: 20px 15px;
          &.not-login-info {
            background: #292b32 url(../../images/v2/energy-rent/energy-info-bg-m.png) no-repeat right bottom;
